import refs from './refs.js';
import colors from './colors.js';

//variables
let { body, btnStart } = refs;
let timerId = null;
let currentColor = '';

export const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

export const randomColor = colorArray => {
  let randomInteger = randomIntegerFromInterval(0, colors.length - 1);
  let randomColor = `${colorArray[randomInteger]}`;
  return randomColor;
};

export function handleBtnStart() {
  btnStart.setAttribute('disabled', 'disabled');
  timerId = setInterval(() => {
    let new_color = `${randomColor(colors)}`;
    while (new_color === currentColor) {
      new_color = `${randomColor(colors)}`;
    }
    body.style.backgroundColor = new_color;
    currentColor = new_color;
  }, 1000);
}

export function handleBtnStop() {
  btnStart.removeAttribute('disabled', 'disabled');
  clearInterval(timerId);
}
